


































import {getArrayObjIndexByKey,isEmpty} from '@/utils'
import {FormatMixin} from '@/assets/mixin'
import { Component, Vue, Mixins} from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Mixins(FormatMixin) {
  /**data */
  selectedLabel = '全部'
  tabs = [
    {
      label: '全部',
      key: '1;2;3;4;5',
      // icon: 'cubeic-like',
      list: []
    },
    {
      label: '还款中',
      key: '4',
      // icon: 'cubeic-star',
      list: []
    },
    {
      label: '还款完成',
      key: '5',
    }
  ]

  /**life hook */
  created(){
    this.changeHandler(this.selectedLabel)
  }

  /**method */
  // clickHandler(label:any) {
  //   this.changeHandler(label)
  // }
  // 点击与自身不同的其他导航 
  async changeHandler(label:any) { // ***待优化：触底加载更多
    const index = getArrayObjIndexByKey(this.tabs,{label})
    // console.log('index：',index)
    const obj:any = this.tabs[index]
    console.log('obj',obj)
    const list = obj ? (isEmpty(obj.list) ? [] : obj.list) : []
    console.log('list',list)
    if(list.length === 0){
      const status = obj.key
      const data = await this.$request({
        api:'userInvestRecord',
        params:{
          token: this.$store.state.token,
          status,
          page:1,
          size:100,
        }
      })
      if(data.resCode === '0000'){
        this.tabs[index].list = data.list
      }
    }
  }
}
